"use client";
import React, { useState } from 'react';
import Link from 'next/link';
import Button from './Button';
import { ToastProvider } from './ToastContext';
import ToastView from './ToastView';

export default function Layout({ children }: { children: React.ReactNode }) {
  const [open, setOpen] = useState(false);

  return (
    <div className="min-h-screen flex flex-col bg-white">
      <header className="sticky top-0 z-40 bg-white border-b border-slate-100 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 lg:px-8">
          <div className="flex justify-between items-center h-20">
            <Link href="/" className="text-2xl font-bold bg-linear-to-r from-indigo-600 to-blue-600 bg-clip-text text-transparent hover:opacity-80 transition-opacity">
              Clinics
            </Link>

            <nav className="hidden md:flex items-center gap-10">
              <Link href="/clinics" className="text-slate-600 hover:text-indigo-600 font-medium text-sm transition-colors duration-200">
                Clinics
              </Link>
              <Link href="/doctors" className="text-slate-600 hover:text-indigo-600 font-medium text-sm transition-colors duration-200">
                Doctors
              </Link>
              <Link href="/about" className="text-slate-600 hover:text-indigo-600 font-medium text-sm transition-colors duration-200">
                About
              </Link>
              <Link href="/login">
                <Button size="sm">Login</Button>
              </Link>
            </nav>

            <div className="md:hidden flex items-center">
              <button
                aria-label="Toggle menu"
                onClick={() => setOpen(!open)}
                className="p-2.5 rounded-lg hover:bg-slate-100 transition-colors"
              >
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-slate-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d={open ? 'M6 18L18 6M6 6l12 12' : 'M4 6h16M4 12h16M4 18h16'} />
                </svg>
              </button>
            </div>
          </div>

          {open && (
            <div className="md:hidden border-t border-slate-100 py-4">
              <div className="flex flex-col gap-1">
                <Link href="/clinics" className="px-4 py-2.5 rounded-lg hover:bg-slate-100 text-slate-700 transition-colors">
                  Clinics
                </Link>
                <Link href="/doctors" className="px-4 py-2.5 rounded-lg hover:bg-slate-100 text-slate-700 transition-colors">
                  Doctors
                </Link>
                <Link href="/about" className="px-4 py-2.5 rounded-lg hover:bg-slate-100 text-slate-700 transition-colors">
                  About
                </Link>
                <Link href="/login" className="px-4 py-2.5 rounded-lg hover:bg-slate-100 text-slate-700 transition-colors">
                  Login
                </Link>
              </div>
            </div>
          )}
        </div>
      </header>

      <main className="flex-1">
        <ToastProvider>
          {children}
          <ToastView />
        </ToastProvider>
      </main>

      <footer className="bg-slate-950 text-slate-400 py-16 mt-32 border-t border-slate-800">
        <div className="max-w-7xl mx-auto px-4 lg:px-8">
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-12 mb-12">
            <div>
              <h3 className="text-white font-bold text-base mb-4">Clinics</h3>
              <ul className="space-y-3 text-sm">
                <li><Link href="/clinics" className="hover:text-white transition-colors duration-200">All Clinics</Link></li>
                <li><Link href="/doctors" className="hover:text-white transition-colors duration-200">Find Doctors</Link></li>
              </ul>
            </div>
            <div>
              <h3 className="text-white font-bold text-base mb-4">For Patients</h3>
              <ul className="space-y-3 text-sm">
                <li><Link href="/" className="hover:text-white transition-colors duration-200">How it works</Link></li>
                <li><Link href="/" className="hover:text-white transition-colors duration-200">FAQ</Link></li>
              </ul>
            </div>
            <div>
              <h3 className="text-white font-bold text-base mb-4">Company</h3>
              <ul className="space-y-3 text-sm">
                <li><Link href="/" className="hover:text-white transition-colors duration-200">About</Link></li>
                <li><Link href="/" className="hover:text-white transition-colors duration-200">Contact</Link></li>
              </ul>
            </div>
            <div>
              <h3 className="text-white font-bold text-base mb-4">Legal</h3>
              <ul className="space-y-3 text-sm">
                <li><Link href="/" className="hover:text-white transition-colors duration-200">Privacy</Link></li>
                <li><Link href="/" className="hover:text-white transition-colors duration-200">Terms</Link></li>
              </ul>
            </div>
          </div>
          <div className="border-t border-slate-800 pt-8 text-center text-xs text-slate-500">
            Â© 2025 Clinics Booking System. All rights reserved.
          </div>
        </div>
      </footer>
    </div>
  );
}
